
import Panel from 'vux/src/components/panel/index.vue'


export default {
name: 'Home',

components: {
Panel
},

data() {
return{
amount: '0',
type: '5',
list: []
}
},

methods: {

},

created(){

},

mounted(){
this.defines.setName('我参与的活动')
var _this = this
this.axios.get('/project/theProjectIAttend')
.then(function(response){
console.log(response, 'home success');
var data = response.data.data
_this.amount = data.length

for(var i = 0; i < data.length; ++i){
_this.list.push(
{
'src': data[i].picture_url,
'title': data[i].title,
'desc': data[i].description,
'url': '/enroll/' + data[i].id,
'meta': {
'source': data[i].latitude_longitude,
'date': data[i].location,
'other': data[i].time
}
}
)
}
})
.catch(function(error){
console.log(error, 'home fail');
});
}
}




