
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'


export default {
name: 'Home',

components: {
Group,
Cell,
Panel,
Divider,
XButton,
XHeader,
Search
},

data() {
return{
cookieV: '',
type: '5',
list: [],
results: [],
value: 'test'
}
},

methods: {
clickMe(){

var _this = this
this.axios.get('/user/check_login')
.then(function (response){
console.log(response, 'release page success')
if(response.data.code != '200'){
window.location = 'http://h5.jayna.fun/user/weibo_login'
}
else{
_this.$router.push(`/release`);
}
})
.catch(function (response){
console.log(response, 'release page failure')
});

},



resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult (val) {
console.log('on-change', val)
this.results = val ? getResult(this.value) : []
},
onSubmit () {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
},

created(){

},

mounted(){
this.defines.setName('@趣玩活动中心@')
var _this = this
this.axios.get('/project/allActivity')
.then(function(response){
console.log(response, 'home success');
if(response == undefined || response == null){
console.log('response is null')
}

var data = response.data.data



for(var i = 0; i < data.length; ++i){
_this.list.push(
{
'src': data[i].picture_url,
'title': data[i].title,
'desc': data[i].description,
'url': '/enroll/' + data[i].id,
'meta': {
'source': data[i].latitude_longitude,
'date': data[i].location,
'other': data[i].time
}
}
)
}
})
.catch(function(error){
console.log(error, 'home fail');
});
}
}

function getResult (val) {
let rs = []
for (let i = 0; i < 20; i++) {
rs.push({
title: `${val} result: ${i + 1} `,
other: i
})
}
return rs
}




