
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'
import Divider from 'vux/src/components/divider/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'


export default {
name: 'Release',

components: {
Group,
GroupTitle,
Cell,
XInput,
Selector,
PopupPicker,
XAddress,
Datetime,
XNumber,
XTextarea,
XSwitch,
XButton,
Grid,
GridItem,
Divider,
PopupRadio
},

data () {
return {
screen_name: '',
gender: '',
description: '',
mySrc: '',
newFile: null,
newImgUrl: '',
profile_Image_url: ''
}
},

mounted(){
this.defines.setName('个人信息')
var _this = this
this.axios.get('/user/message')
.then(function(response){
console.log(response, 'profile success')
var form = response.data.data
_this.screen_name = form.screen_name
if(form.gender == 'm'){
_this.gender =  '男'
}
else{
_this.gender = '女'
}
if(form.description == ''){
_this.description = '你很懒，什么也没留下'
}
_this.description = form.description
_this.mySrc = form.profile_Image_url
})
.catch(function(error){
console.log(error, 'profiel error');
});
},

methods: {
submit: function(){
var _this = this

this.axios.get('http://101.132.181.76:3838/')
.then(function(response){
console.log(response, 'qiuniu tokne success')
var qiniu = require('qiniu-js')

var file = _this.newFile
var token = response.data.token
var key = null
var putExtra = {
fname: "",
params: {},
mimeType: null
}
var config = {
useCdnDomain: false,
region: null
}

var observable = qiniu.upload(file, key, token, putExtra, config)
console.log(observable, 'observable')

var observer = {
next(res){

},
error(err){
console.log(err, 'observer error')
},
complete(res){
console.log(res, 'observer complete')
_this.newImgUrl = '//pc56nec9k.bkt.clouddn.com/' + res.hash
_this.profile_Image_url = '//pc56nec9k.bkt.clouddn.com/' + res.hash
}
}

var subscription = observable.subscribe(observer)
console.log(subscription, 'subscription')

})
.catch(function(error){
console.log(error, 'qiuniu token fail');
});

this.axios.post('/user/update',{
profile_Image_url: _this.profile_Image_url
})
.then(function(response){
console.log(response)
alert('提交成功, 过一会儿再试')
})
.catch(function(error){
alert("error")
console.log(error);
});
},
getFile(e){
let _this = this;
var file = e.target.files[0]
if(!e || !window.FileReader){
alert("浏览器不支持上传文件或者图片文件已损坏")
return
}
_this.newFile = file
let reader = new FileReader()
reader.readAsDataURL(file)
reader.onloadend = function(){
_this.mySrc = this.result
}

},
loginout(){
alert('后端接口还没实现')



}
}
}



