
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'


export default {
name: 'Release',

components: {
Group,
GroupTitle,
Cell,
XInput,
Selector,
PopupPicker,
XAddress,
Datetime,
XNumber,
XTextarea,
XSwitch,
XButton,
Grid,
GridItem,
},

data () {
return {
max10: 10,
max50: 50,
subject: '',
location: '',
datetime: '',
detail: '',
addressCode: [],
address: '',
addressData: ChinaAddressData,
mySrc: 'http://owj98yrme.bkt.clouddn.com//nab/6.jpg',
newFile: null,
newImgUrl: '',
label: '更换默认主题图片'
}
},

methods: {
publish: function(){
this.subject  = this.subject.trim()
this.location = this.location.trim()
if(this.subject == '' || this.address == '' || this.location == ''){
alert("主题、地址和地点为必填项")
return
}



var _this = this

this.axios.get('http://101.132.181.76:3838/')
.then(function(response){
console.log(response, 'qiuniu tokne success')
var qiniu = require('qiniu-js')

var file = _this.newFilex
var token = response.data.token
var key = null
var putExtra = {
fname: "",
params: {},
mimeType: null
}
var config = {
useCdnDomain: false,
region: null
}

var observable = qiniu.upload(file, key, token, putExtra, config)
console.log(observable, 'observable')

var observer = {
next(res){

},
error(err){
console.log(err, 'observer error')
},
complete(res){
console.log(res, 'observer complete')
_this.newImgUrl = 'pc56nec9k.bkt.clouddn.com/' + res.hash
}
}
var subscription = observable.subscribe(observer)
})
.catch(function(error){
console.log(error, 'qiuniu token fail');
});

this.axios.post('/project/create', {
title: _this.subject,
time: _this.datetime,
location: _this.location,
latitude_longitude: _this.address,
picture: _this.newImgUrl,
description: _this.detail
}).then(function (response) {
console.log(response, 'push project data to database suceess');
var activityId = response.data.data.id

_this.$router.push(`/enroll/${activityId}`);
}).catch(function (error) {
console.log(error, 'erorr from push project');
});
},

getFile(e){
let _this = this;
var file = e.target.files[0]
if(!e || !window.FileReader){
alert("浏览器不支持上传文件或者图片文件已损坏")
return
}
_this.newFile = file
let reader = new FileReader()
reader.readAsDataURL(file)
reader.onloadend = function(){
_this.mySrc = this.result
_this.label = '更换主题图片'
}
},
},

watch: {
addressCode: function() {
this.address = value2name(this.addressCode, ChinaAddressV4Data)
},
},

mounted(){
this.defines.setName('趣玩活动中心')
}

}



