
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'Photos',

directives: {
TransferDom
},
components: {
Previewer,
Divider,
XButton
},

mounted(){
this.defines.setName('活动相关照片')
var url = document.location.toString(); 
if(url.indexOf('?') != -1){
url = url.substring(url.indexOf('?')+1, url.length)
kv = url.split('=')
if(kv[0] == 'pid' && kv[1] != null){
this.activityId = kv[1]
}
}

var _this = this

this.axios.get('http://localhost:3003/photos',{
params: {
token: _this.cookieV,
project_id: _this.activityId
}
})
.then(function(response){
var photos = response.data.photos
if(response.data.isLogin == 'true'){
_this.isLogin = true
}
else{
_this.isLogin = false
}


if(response.data.isUpload = 'true'){
_this.isUpload = true
_this._isUpload = !_this.isUpload
_this.mySrc = photos[response.data.index]
}
else{
_this.isUpload = false
_this._isUpload = !_this.isUpload
}

_this.list = new Array([photos.length])

for(var i = 0; i < photos.length; ++i){
_this.list.push({
'msrc': photos[i],
'src': photos[i]
})
}
console.log(_this.list, "!!!!!!!!")
})
.catch(function(error){
alert("error")
console.log(error);
});
},

methods: {
logIndexChange (arg) {
console.log(arg)
},
show (index) {
this.$refs.previewer.show(index)
},

getFile(e){
let _this = this;
var file = e.target.files[0]
if(!e || !window.FileReader){
alert("浏览器不支持上传文件或者图片文件已损坏")
return
}
let reader = new FileReader()

reader.readAsDataURL(file)
reader.onloadend = function(){
for(var i = 0; i < _this.list.length; ++i){






}
}

},

},
data () {
return {
CookieV: '',
activityId: '',
isLogin: false,
isUpload: true,
_isUpload: false,
mySrc: '',
list: [],

options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
},
}
}
}



