
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'


export default {
components: {
Group,
Cell,
XInput,
Selector,
PopupPicker,
XAddress,
Datetime,
XNumber,
XTextarea,
XSwitch,
XButton,
ChinaAddressData,

Grid,
GridItem,
Divider,
Card,
Checklist,
Checker,
CheckerItem,
PopupRadio
},

mounted(){
this.defines.setName('活动计划')
var url = document.location.toString(); 
console.log(url, 'manager url')
if(url.lastIndexOf('/') != -1){
var pid = url.substring(url.lastIndexOf('/')+1, url.length)
this.activityId = pid
}
this.toMembers = '/project/relationPeople/' + this.activityId


var _this = this
this.axios.get('/project/manager/' + _this.activityId)
.then(function(response){
var form = response.data.data[0]
console.log(form, '~!!')

_this.subject = form.title
_this.location = form.location
_this.datetime = form.time
_this.detail = form.description
_this.addressCode = form.latitude_longitude
_this.mySrc = form.picture
if(form.status != null){
_this.status = form.status
_this.statusMsg = form.status
}
else{
_this.statusMsg = '点击修改活动状态'
}
})
.catch(function(error){
alert("error")
console.log(error);
});

},

data() {
return {
activityId: '',
subject: '',
location: '',
datetime: '1999-10-12 16:00',
detail: '',
addressCode: [],
oadc: [],
address: '',
addressData: ChinaAddressData,

status: '',
toMembers: '',
toPhotos: '',
toManager: '',
statusMsg: '',



mySrc: '',
newFile: null,
newImgUrl: '',
profile_Image_url: '',

option2: '',
options2: [
{
key: 'A',
value: '尚未开始'
},
{
key: 'B',
value: '正在进行'
},
{
key: 'C',
value: '报名结束'
},
{
key: 'D',
value: '已经取消'
}
],
}
},

methods: {
getFile(e){
let _this = this;
var file = e.target.files[0]
if(!e || !window.FileReader){
alert("浏览器不支持上传文件或者图片文件已损坏")
return
}
_this.newFile = file
let reader = new FileReader()
reader.readAsDataURL(file)
reader.onloadend = function(){
_this.mySrc = this.result
}
},
change(){
this.address = value2name(this.oadc, ChinaAddressV4Data)
},

submit: function(){
var _this = this

this.axios.get('http://101.132.181.76:3838/')
.then(function(response){
console.log(response, 'qiuniu tokne success')
var qiniu = require('qiniu-js')

var file = _this.newFile
var token = response.data.token
var key = null
var putExtra = {
fname: "",
params: {},
mimeType: null
}
var config = {
useCdnDomain: false,
region: null
}

var observable = qiniu.upload(file, key, token, putExtra, config)
console.log(observable, 'observable')

var observer = {
next(res){

},
error(err){
console.log(err, 'observer error')
},
complete(res){
console.log(res, 'observer complete')
_this.newImgUrl = '//pc56nec9k.bkt.clouddn.com/' + res.hash
_this.profile_Image_url = '//pc56nec9k.bkt.clouddn.com/' + res.hash
}
}

var subscription = observable.subscribe(observer)
console.log(subscription, 'subscription')

})
.catch(function(error){
console.log(error, 'qiuniu token fail');
});



this.axios.post('/project/update',{
id: _this.activityId,
title: _this.subject,
time: _this.datetime,
location: _this.location,
latitude_longitude: _this.address,
status: _this.status,
picture: _this.profile_Image_url
}).then(function(response){
if(response.data.code == '200'){
console.log(response, 'update success')
_this.$router.push(`/enroll/${_this.activityId}`);

}
else{
console.log(response, 'update success, but status error')
}
})
.catch(function(error){
console.log(error);
});
},

},







watch: {
addressCode: function() {


},
option2: function(){
if(this.option2 == "A"){
this.status = '尚未开始'
}
else if(this.option2 == "B"){
this.status = '正在进行'
}
else if(this.option2 == "C"){
this.status = '报名结束'
}
else if(this.option2 == "D"){
this.status = '已经取消'
}
this.statusMsg = '点击修改活动状态'
}
}

}



