
import Divider from 'vux/src/components/divider/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default{
name: 'Enroll',
components: {
Divider,
Group,
Cell,
XButton,
XInput
},


data() {
return {
CookieV: '',
activityId: '',

releaser: 'randomer',
datetime: 'a',
subject: 'oo',
address: 'oooo',
location: 'cc',

isManager: true,
signin: true,
cancel: false,
toMembers: '',
toPhotos: '',
toManager: '',
}
},

methods: {
enroll(){
var _this = this


this.axios.post('/project/attend',{
activity_id: _this.activityId
}).
then(function (response) {
console.log(response);
_this.signin = !_this.signin
_this.cancel = !_this.cancel
}).catch(function (error) {
alert(error.code + ": " + error.msg)
console.log(error);
});
},

_cancel(){

var _this = this

this.axios.post('/project/cancel', {
activity_id: _this.activityId
}).then(function (response) {
console.log(response);
_this.signin = !_this.signin
_this.cancel = !_this.cancel

}).catch(function (error) {
alert(error.code + ": " + error.msg)
console.log(error);
});
}

},

mounted(){
this.defines.setName('趣玩报名中心')

var url = document.location.toString(); 
if(url.lastIndexOf('/') != -1){
var pid = url.substring(url.lastIndexOf('/')+1, url.length)
this.activityId = pid
}

this.toMembers = '/relationPeople/' + this.activityId

this.toManager = '/manager/' + this.activityId


var _this = this;
if(this.activityId != ''){
this.axios.get('/project/detailed/' + this.activityId ).
then(function (response) {
console.log(response);
_this.releaser = response.data.data.creator
_this.subject = response.data.data.title
_this.datetime = response.data.data.time
_this.address = response.data.data.latitude_longitude
_this.location = response.data.data.location

if(response.data.data.isManager == 'true'){
_this.isManager = true
}
else{
_this.isManager = false
}


if(response.data.data.status == 'true'){
_this.signin = false
_this.cancel = true
}
else{
_this.signin = true
_this.cancel = false
}



}).catch(function (error) {

console.log(error, 'enroll error');
});
}
}
}



