export default {
  name: 'App',

    beforeCreate(){
       var cookies = document.cookie.split(";")
        for(var i = 0; i < cookies.length; ++i){
          var kv = cookies[i].split("=");
          if(kv[0] == 'panda'){
            alert('test cookie: ' + kv[1]);
            this.defines.setCookie(kv[1])
          }
        }
    },
}



