
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'Members',

components: {
Group,
Cell,
Panel,
Divider,
XButton,
},

data() {
return{
activityId: '',
amount: '0',
type: '5',
list: []
}
},

methods: {

},

mounted(){
this.defines.setName('活动参与人员')
var url = document.location.toString();
if(url.lastIndexOf('/') != -1){
var pid = url.substring(url.lastIndexOf('/')+1, url.length)
this.activityId = pid
}
var _this = this
this.axios.get('/project/relationPeople/' + _this.activityId)
.then(function(response){
console.log(response);
var form = response.data.data
_this.amount = form.length

for(var i = 0; i < form.length; ++i){
_this.list.push({
'src': form[i].picture_url,
'title': form[i].screen_name + '(' + form[i].gender + ')',
'desc': form[i].description
})
}

})
.catch(function(error){
console.log(error);
});
}
}




